/* Основной контейнер */
.main-menu {
    background-color: #242222b8; /* Светлый фон */
    color: #333; /* Тёмный текст */
    padding: 20px;
    min-height: 100vh; /* Высота экрана */
    font-family: 'Arial', sans-serif;
  }
  
  /* Заголовок */
  .main-menu-title {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 20px;
  }
  
  /* Контейнер карточек */
  .menu-container {
    display: grid; /* Адаптивная сетка */
    grid-template-columns: repeat(2, 1fr); /* Две колонки */
    gap: 20px; /* Расстояние между карточками */
    justify-content: center; /* Центрирование сетки */
  }
  
  /* Карточки */
  .menu-card {
    background: linear-gradient(135deg, #ffdead, #bfbcb1); /* Градиентный фон карточки */
    border-radius: 10px; /* Закругленные углы */
    overflow: hidden; /* Обрезка содержимого */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Лёгкая тень */
    cursor: pointer;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    height: 200px; /* Фиксированная высота карточки */
    display: flex; /* Flexbox для центрирования */
    align-items: center; /* Центрирование изображения по вертикали */
    justify-content: center; /* Центрирование изображения по горизонтали */
  }
  
  .menu-card:hover {
    transform: scale(1.05); /* Лёгкое увеличение при наведении */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Усиленная тень */
  }
  
  /* Изображения */
  .card-image {
    width: 100%; /* Изображение заполняет всю ширину карточки */
    height: auto; /* Сохраняем пропорции */
    object-fit: cover; /* Полное заполнение карточки */
  }
  